import { React, useContext } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { getCurrentUser, userRegister, UserContext } from "../store/userStore";

const Navbar = () => {
  const [state, dispatch] = useContext(UserContext);
  const navigate = useNavigate();
  const handlerLogout = () => {
    console.log("logout");
    dispatch({
      type: "LOGOUT",
      payload: {
        update_id: userRegister.findIndex(
          (item) => item.username === getCurrentUser.username
        ),
      },
    });
    navigate("/login");
  };
  return (
    <>
      <nav
        className="navbar navbar-expand-lg bg-body-tertiary mb-5 sticky-top"
        // style={{ bottom: "10px" }}
      >
        <div className="container-fluid">
          <NavLink className="navbar-brand" to="/">
            Navbar
          </NavLink>

          {state.username !== "" && (
            <div className="collapse navbar-collapse ">
              <ul className="navbar-nav ">
                <li className="nav-item">
                  <NavLink className="nav-link" to="/album/search">
                    
                  </NavLink>
                </li>
              </ul>

              <ul className="navbar-nav ">
                <li className="nav-item">
                  <NavLink className="nav-link" to="/collect">
                    
                  </NavLink>
                </li>
              </ul>
              <ul className="navbar-nav ">
                <li className="nav-item">
                  <NavLink className="nav-link" to="/profile">
                    
                  </NavLink>
                </li>
              </ul>
            </div>
          )}

          <div className="collapse navbar-collapse justify-content-end">
            {state.username === "" && (
              <ul className="navbar-nav">
                <li className="nav-item">
                  <NavLink className="nav-link" aria-current="page" to="/login">
                    
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/register">
                    
                  </NavLink>
                </li>
              </ul>
            )}
            {state.username !== "" && (
              <ul className="navbar-nav">
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    aria-current="page"
                    onClick={handlerLogout}
                  >
                    
                  </NavLink>
                </li>
              </ul>
            )}
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
